import React, { useEffect } from "react";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import AdminNavs from "../../components/Admin/AdminNavs";
import {
  ReportDelete,
  ReportHasUpdated,
  ReportsList,
} from "../../actions/ReportAction";

const AdminReports = () => {
  const dispatch = useDispatch();
  const { reports, loading, error } = useSelector((state) => state.reportList);
  const {
    success,
    loading: confirmLoading,
    error: errorComfirmation,
  } = useSelector((state) => state.reportConfirm);
  const {
    success: deleteSuccess,
    loading: deleteLoading,
    error: deleteError,
  } = useSelector((state) => state.reportDelete);

  const deleteHandler = (id) => {
    if (window.confirm("This may help you in futer")) {
      dispatch(ReportDelete(id));
      // {deleteSuccess && toast.success("Report Removed")}
      if (deleteSuccess) {
        toast.success("Report Removed");
      }
    }
  };

  const confirmHandler = (id) => {
    dispatch(ReportHasUpdated(id));
    // {success && toast.success(`Email has been sent to ${x.userId}`)}
    if (success) {
      toast.success(`Email has been sent to ${id}`);
    }
  };

  useEffect(() => {
    dispatch(ReportsList());
  }, [dispatch, success, deleteSuccess]);

  return (
    <>
      <AdminNavs />
      {errorComfirmation && toast.error(errorComfirmation)}
      {deleteError && toast.error(deleteError)}

      <header aria-label="Page Header" className="bg-gray-50">
        <div className="mx-auto max-w-screen-xl px-4 py-8 sm:px-6 lg:px-8">
          <div className="mt-8 pb-10">
            <h1 className="text-2xl font-bold text-gray-900 sm:text-3xl">
              All Reports
            </h1>

            <p className="mt-1.5 text-sm text-gray-500">
              Search Products, Delete Products, Edit Products Keep it up! ðŸš€
            </p>
          </div>
        </div>
      </header>

      {loading && <p>loading...</p>}
      {error && toast.error(error)}

      <section class="text-gray-600 bg-gray-50 body-font">
        <div class="container px-5 mx-auto">
          <div class="flex flex-wrap -m-4">
            {reports.length === 0 ? (
              <p class="py-10">No any report recieved </p>
            ) : (
              reports.map((x) => (
                <div class="p-4 md:w-1/2 w-full">
                  <div class="h-full bg-gray-100 p-8 rounded">
                    <p class="leading-relaxed mb-6">
                      <strong>Order ID</strong> :
                      <Link to={`/admin/order/${x.orderId}`}>{x.orderId}</Link>
                    </p>

                    <p class="leading-relaxed mb-6">
                      <strong>Type </strong> : {x.typeReport}
                    </p>

                    <p class="leading-relaxed mb-6">
                      <strong>Ordered Data </strong> :
                      {x.updatedAt.substring(0, 10)}
                    </p>

                    <p class="leading-relaxed mb-6">
                      <strong>Confirm </strong> :{" "}
                      {x.confirmation ? "Ture" : "False"}
                    </p>

                    <button
                      class="flex mx-auto text-white   bg-indigo-500 border-0 py-2 px-8 focus:outline-none hover:bg-indigo-600 rounded text-lg"
                      onClick={() => confirmHandler(x._id)}
                    >
                      <strong>Confirm it </strong> : Confirmation{" "}
                      {confirmLoading && <p>loading...</p>}
                    </button>

                    {x.confirmation && (
                      <button
                        class="flex mx-auto text-white   bg-red-500 border-0 py-2 px-8 focus:outline-none hover:bg-red-600 rounded text-lg"
                        onClick={() => deleteHandler(x._id)}
                      >
                        {" "}
                        Delelte{" "}
                      </button>
                    )}

                    <a class="inline-flex items-center">
                      <img
                        alt="testimonial"
                        src="https://dummyimage.com/106x106"
                        class="w-12 h-12 rounded-full flex-shrink-0 object-cover object-center"
                      />
                      <span class="flex-grow flex flex-col pl-4">
                        <span class="title-font font-medium text-blue-900">
                          <Link to={`/admin/user/${x.userId}`}>{x.userId}</Link>
                        </span>
                      </span>
                    </a>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      </section>
    </>
  );
};

export default AdminReports;
